import { Component, OnInit, ViewChild } from '@angular/core';
import { CommitOrderData } from '../../_models/CommitOrderData';
import { Processor } from '../../_models/Processor';
import { PartParoduct } from '../../_models/PartParoduct';
import { DatePipe } from '@angular/common';
import { RequirementService } from '../../_services/requirement.service';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { Subject } from 'rxjs';
import { RequirementData } from '../../_models/RequirementData';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { DataTableDirective } from 'angular-datatables';
import { COLDetailsList } from '../../_models/COLDetailsList';
import { ChatBox } from '../../_models/ChatBox';

@Component({
  selector: 'app-commit-order',
  templateUrl: './commit-order.component.html',
  styleUrls: ['./commit-order.component.css']
})
export class CommitOrderComponent implements OnInit {
  model: any = {};
  mymodel: any = {};
  editmodel: any = {};
  updatemodel: any = {};
  chatmodel: any = {};
  chats: ChatBox[];
  roleid: any =  Number(String(localStorage.getItem('RoleID')));
  userid: any =  Number(localStorage.getItem('UserId'));
  makes: PartParoduct[];
  models: PartParoduct[];
  processors: Processor[];
  orders: CommitOrderData[];
  coldetails: COLDetailsList[];
  check1: boolean;
  check2: boolean;
  check3: boolean;
  check4: boolean;
  filter1: any = {};
  filter2: any = {};
  filter3: any = {};
  filter4: any = {};
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  @ViewChild('infoModal') public infoModal: ModalDirective;
  @ViewChild('editOrder') public editOrder: ModalDirective;
  @ViewChild('UpdatePOModal') public UpdatePOModal: ModalDirective;
  @ViewChild('COLDetails') public COLDetails: ModalDirective;
  @ViewChild('ChatModal') public ChatModal: ModalDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
};
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<RequirementData> = new Subject();

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService,
    private requirementservice: RequirementService, public datepipe: DatePipe) { }

  ngOnInit(): void {
    this.model.req_type = '0';
    this.check1 = true;
    this.check2 = false;
    this.check3 = false;
    this.check4 = false;
    this.filter1 = 1;
    this.filter2 = 0;
    this.filter3 = 0;
    this.filter4 = 0;
    this.loadMakes();
    this.loadProcessors();
    this.loadOrders();
  }
  loadMakes() {
    return this.loadspecsService.getMakes().subscribe((partproducts: PartParoduct[]) => {
      this.makes = partproducts.filter(proj => (proj.type === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadModels(value: number) {
    return this.loadspecsService.getModels().subscribe((partproducts: PartParoduct[]) => {
      this.models = partproducts.filter(proj => (Number(proj.parentId) === Number(value)));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadProcessors() {
    return this.loadspecsService.getProcessors().subscribe((processors: Processor[]) => {
      this.processors = processors;
    }, error => {
      this.alertify.error(error);
    });
  }
  loadOrders() {
    return this.requirementservice.getCommitOrders().subscribe((orders: CommitOrderData[]) => {
      this.orders = orders;
      console.log(this.orders);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });

  }
  addOrder() {
    this.model.emp_id = localStorage.getItem('UserId');
    console.log(this.model);
    if (!this.model.o_num) {
      this.alertify.error('Enter Order Number');
      return;
    }
    if (this.model.make_id === null) {
      this.alertify.error('Select Make');
      return;
    }
    if (this.model.model_id === null) {
      this.alertify.error('Select Model');
      return;
    }
    if (this.model.proc_id === null) {
      this.alertify.error('Select Processor');
      return;
    }
    if (this.model.o_type === '0') {
      this.alertify.error('Select Type');
      return;
    }
    if (!this.model.co_edd) {
      this.alertify.error('Select EDD');
      return;
    }
    if (!this.model.co_qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.model.c_name) {
      this.alertify.error('Enter Customer Name');
      return;
    }
    console.log(this.model);
    this.requirementservice.addCommitOrder(this.model).subscribe((orders: CommitOrderData[]) => {
      this.loadOrders();
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
      this.model.make_id = null;
      this.model.model_id = null;
      this.model.proc_id = null;
      this.model.co_edd = '';
      this.model.o_num = '';
      this.model.c_name = '';
      this.model.comment = '';
      this.model.o_type = '0';
      this.infoModal.hide();
      this.alertify.success('Order Created Successfully');
    }, error => {
      this.alertify.error(error);
    });
  }

  EditOrder(id: number, date: Date) {
    this.editmodel = this.orders.filter(proj => (Number(proj.id) === Number(id)))[0];
    this.editmodel.co_edd = this.datepipe.transform(this.editmodel.co_edd, 'yyyy-MM-dd');
    this.editmodel.comment = '';
    this.loadModels(this.editmodel.make_id);
    this.editOrder.show();
  }
  EditOrderPost(id: number, date: Date) {
    if (this.editmodel.o_type === '0') {
      this.alertify.error('Select Type');
      return;
    }
    if (!this.editmodel.co_edd) {
      this.alertify.error('Select EDD');
      return;
    }
    if (!this.editmodel.co_qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.editmodel.c_name) {
      this.alertify.error('Enter Customer Name');
      return;
    }
    this.editmodel.updated_by = Number(localStorage.getItem('UserId'));
    console.log(this.editmodel);
    this.requirementservice.EditCommitOrderPost(this.editmodel).subscribe(() => {
      this.loadOrders();
      this.editmodel.id = '';
      this.editmodel.e_edd = '';
      this.editmodel.updated_by = '';
      this.editOrder.hide();
      this.alertify.success('Order Edited Successfully');
    }, error => {
      this.alertify.error(error);
    });
  }
  UpdateCPOModal (id: number, co_status: number) {
    this.updatemodel.id = id;
    this.updatemodel.co_status = co_status;
    console.log(this.updatemodel);
    this.UpdatePOModal.show();
  }
  UpdateCOPost() {
    this.updatemodel['updated_by'] = localStorage.getItem('UserId');
    console.log(this.updatemodel);
    if (!this.updatemodel.comment) {
      this.alertify.error('Enter Comment');
      return;
    }
    this.requirementservice.UpdateCOPost(this.updatemodel).subscribe((orders: CommitOrderData[]) => {
      this.loadOrders();
      this.updatemodel = {};
      this.UpdatePOModal.hide();
      this.alertify.success('Order Edited Successfully');
    }, error => {
      this.alertify.error(error);
    });

  }

  BookCPOModal(id: number, co_status: number) {
    this.updatemodel['updated_by'] = localStorage.getItem('UserId');
    this.updatemodel.id = id;
    this.updatemodel.co_status = co_status;
    this.requirementservice.BookCOPost(this.updatemodel).subscribe((orders: CommitOrderData[]) => {
      this.loadOrders();
      this.updatemodel = {};
      this.UpdatePOModal.hide();
      this.alertify.success('Order Booked Successfully');
    }, error => {
      this.alertify.error(error);
    });

  }

  CloseCPOModal(id: number, co_status: number) {
    this.updatemodel['updated_by'] = localStorage.getItem('UserId');
    this.updatemodel.id = id;
    this.updatemodel.co_status = co_status;
    this.requirementservice.CloseCOPost(this.updatemodel).subscribe((orders: CommitOrderData[]) => {
      this.loadOrders();
      this.updatemodel = {};
      this.UpdatePOModal.hide();
      this.alertify.success('Order Closed Successfully');
    }, error => {
      this.alertify.error(error);
    });

  }
  CheckDetails (id: number) {
    return this.requirementservice.getCODetails(id).subscribe((coldetails: COLDetailsList[]) => {
      this.coldetails = coldetails;
      console.log(this.coldetails);
      this.COLDetails.show();
    }, error => {
      this.alertify.error(error);
    });
  }
  activateModel(event, type: number) {
    console.log(type);
    return this.requirementservice.getCommitOrders().subscribe((orders: CommitOrderData[]) => {
      if (type === 0) {
        this.check1 = false; this.check2 = false; this.check3 = false; this.check4 = false;
        this.filter1 = 1;
        if (event.target.checked === true) {
          console.log('sosos-1');
          this.check1 = true;
          this.orders = orders;
          this.filter2 = 0; this.filter3 = 0; this.filter4 = 0;
        } else {
          console.log('sosos-2');
          this.check1 = false;
          this.orders = orders.filter(proj => (proj.co_status === 100));
          this.filter1 = 0; this.filter2 = 0; this.filter3 = 0; this.filter4 = 0;
        }
      }
      if (type === 1) {
        this.check1 = false; this.filter1 = 0;
        this.filter2 = type;
        if (event.target.checked === false) {
          this.filter2 = 0;
        }
        if (this.filter3 === 0) {
          console.log('sosos-3');
          this.check2 = true;
          this.orders = orders.filter(proj => (proj.co_status === this.filter2 || proj.co_status === this.filter3));
        } else {
          console.log('sosos-4');
          this.check2 = false;
          this.orders = orders.filter(proj => (proj.co_status === this.filter2  && proj.booked_status === this.filter4
            || proj.co_status === this.filter3 && proj.booked_status === this.filter4));
        }
      }
      if (type === 3) {
        this.check1 = false; this.filter1 = 0;
        this.filter3 = type;
        if (event.target.checked === false) {
          this.filter3 = 0;
        }
        if (this.filter4 === 0) {
          console.log('sosos-5');
          this.check3 = true;
          this.orders = orders.filter(proj => (proj.co_status === this.filter2 || proj.co_status === this.filter3));
        } else {
          console.log('sosos-6');
          this.check3 = false;
          this.orders = orders.filter(proj => (proj.co_status === this.filter2  && proj.booked_status === this.filter4
            || proj.co_status === this.filter3 && proj.booked_status === this.filter4));
        }
      }
      if (type === 5) {
        this.filter4 = 1;
        console.log(this.filter1);
        if (this.filter1 === 1) {
            if (event.target.checked === false) {
              this.filter4 = 0;
              this.orders = orders;
            }
            if (event.target.checked === true) {
              console.log('sosos-7');
              this.check4 = true;
              this.orders = orders.filter(proj => (proj.booked_status === this.filter4));
            }
        } else if (this.filter2 === 1 || this.filter3 === 1) {
            if (event.target.checked === false) {
              this.filter4 = 0;
              console.log('sosos-8');
              this.orders = orders.filter(proj => (proj.co_status === this.filter3 || proj.co_status === this.filter2));
            }
            if (event.target.checked === true) {
              console.log('sosos-9');
              this.orders = orders.filter(proj => (proj.co_status === this.filter2 && proj.booked_status === this.filter4
                || proj.co_status === this.filter3 && proj.booked_status === this.filter4));
            }
        } else {
          if (event.target.checked === false) {
            this.filter4 = 0;
            console.log('sosos-10');
            this.orders = orders.filter(proj => (proj.co_status === 100));
          }
          if (event.target.checked === true) {
            console.log('sosos-10');
            this.orders = orders.filter(proj => (proj.co_status === 100));
          }
        }
      }
      console.log(this.check1);
      console.log(this.check2);
      console.log(this.check3);
      console.log(this.check4);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });

  }

  openChatModal(id: number) {
    this.model.Ref_Id = id;
    this.chatmodel.ref_id = id;
    this.chatmodel.user_id = this.userid;
    this.chatmodel.type = 1;
    return this.requirementservice.ShortfallorderviewChats(this.chatmodel).subscribe((chats: ChatBox[]) => {
      this.chats = chats.filter(proj => (Number(proj.status) === 0 && proj.ref_Id === id));
      console.log('s');
      console.log(this.chats);
      this.loadOrders();
      this.ChatModal.show();
  }, error => {
    this.alertify.error(error);
  });
  }

  sendMessagePost() {
    this.model.U_Id = Number(localStorage.getItem('UserId'));
    this.model.type = 1;
    if (!this.model.Text) {
      this.alertify.error('Enter Comment');
      return;
    }
    this.requirementservice.sendShortfalllChatPost(this.model).subscribe((chats: ChatBox[]) => {
      this.chats = chats.filter(proj => (Number(proj.status) === 0 && proj.ref_Id === this.model.Ref_Id));
      this.loadOrders();
      this.model.Text = '';
    }, error => {
      this.alertify.error(error);
    });
  }

}
