import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommitOrderComponent } from './commit-order.component';


const routes: Routes = [
  {
    path: '',
    component: CommitOrderComponent,
    data: {
      title: 'Commit Orders'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CommitOrderRoutingModule { }
